
import React, { Component } from "react";
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../utils";



class DataNotFound extends Component {
    render() {
        return <div className="data-not-found-container">
            <div className="d-flex align-items-center justify-content-center flex-column pt-5">
                <SVG className="data_not_found mb-5"
                    src={toAbsoluteUrl("/error/data_not_found.svg")}
                />
                <h5 className="h2 text-muted m-0">Data Not Available</h5>
            </div>
        </div>
    }
}
export default DataNotFound