    import React from "react";

    const PN = (props) => {

    const { paginateData, isLoading, total, currentPage, limit } = props  

    const onNextNavigateClick = (event) => {  
        
        const {total,limit, maxPage, paginateData,  managePagging } = props  

        let page = 1;
        if (
        paginateData &&
        Object.keys(paginateData).length > 0 &&
        paginateData.currentPage
        ) {
        page = paginateData.currentPage + 1;
        if (page <= paginateData.totalPages) {
            managePagging(1,currentPage,0,0,0,0,0)
        }
        }

        if (
        page &&
        paginateData &&
        Object.keys(paginateData).length > 0 &&
        paginateData.totalPages >= page
        ) {
            const pageCount = parseInt(page) == 1 ? 0 : parseInt(page) - 1;
            const offset = parseInt(limit) * pageCount;
            managePagging(2,0,total, page, limit, maxPage, offset)
        }
    };

    const onPrevNavigateClick = (event) => {  
        const { total,limit, maxPage, paginateData, managePagging } = props  
        let page = 1;
        if (
        paginateData &&
        Object.keys(paginateData).length > 0 &&
        paginateData.currentPage
        ) {
        page = paginateData.currentPage - 1;
        if (page != 0) {
            managePagging(1,page,0,0,0,0,0)
        }
        }

        if (page && page > 0) {
            const pageCount = parseInt(page) == 1 ? 0 : parseInt(page) - 1;
            const offset = parseInt(limit) * pageCount;
            managePagging(2,0,total, page, limit, maxPage, offset)
        }
    };

    const onUpdatePage = (page) => { 
        const { total,limit, maxPage, managePagging } = props  
        const pageCount = parseInt(page) == 1 ? 0 : parseInt(page) - 1;
        const offset = parseInt(limit) * parseInt(pageCount);
        managePagging(3,0,total, page, limit, maxPage, offset)
    };

    const onUpdateDataLimit = (event) => {  
        const { total, maxPage, managePagging } = props  

        const currentPage = 1;
        const pageCount = 0;
        const offset = parseInt(event.target.value) * pageCount;
        managePagging(4,currentPage,total, 0, event.target.value, maxPage, offset)
    };

    return (
        <>
        {!isLoading &&
        total > 0 &&
        paginateData &&
        Object.keys(paginateData).length > 0 ? (
            <div className="w-100 d-flex justify-content-lg-between justify-content-center align-items-center flex-wrap py-sm-4 px-0 pt-3 ">
                <div className="d-flex flex-wrap py-sm-2 me-sm-3 justify-content-sm-normal justify-content-center">
                    <a className="btn btn-icon btn-sm btn-light btn-hover-dark me-2 my-1 f-center" onClick={(e) => onPrevNavigateClick(e)}>
                    <i className="fa-solid fa-caret-left icon-md"></i>
                    </a>

                    {paginateData.pages && paginateData.pages.length > 0
                    ? paginateData.pages.map((item, pagesIndex) => {
                        return parseInt(pagesIndex) < 3 ? (
                            <a onClick={() => onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light btn-hover-dark f-center ${ paginateData.currentPage == item ? `active` : `` }  me-2 my-1`} > {item} </a>
                        ) : parseInt(pagesIndex) < 4 ? (
                            currentPage < 6 ? (
                            <a onClick={() => onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light btn-hover-dark f-center ${ paginateData.currentPage == item ? `active` : `` }  me-2 my-1`} > {item} </a>
                            ) : (
                            <a className="btn btn-icon btn-sm border-0 btn-hover-dark me-2 my-1"> ... </a>
                            )
                        ) : parseInt(pagesIndex) < 5 ? (
                            currentPage < 6 ? (
                            <a onClick={() => onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-dark f-center ${ paginateData.currentPage == item ? `active` : `` }  me-2 my-1`} > {item} </a>
                            ) : (
                            parseInt(paginateData.pages.length) > currentPage && (
                                <a onClick={() => onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light btn-hover-dark f-center ${ paginateData.currentPage ? `active` : `` }  me-2 my-1`} > {currentPage} </a>
                            )
                            )
                        ) : parseInt(paginateData.pages.length) - 2 == pagesIndex ? (
                            parseInt(paginateData.pages.length) - 1 > currentPage && (
                            <a className="btn btn-icon btn-sm border-0 btn-hover-dark me-2 my-1"> ... </a>
                            )
                        ) : parseInt(paginateData.pages.length) - 1 == pagesIndex ? (
                            <a onClick={() => onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-dark f-center ${ paginateData.currentPage == item ? `active` : `` }  me-2 my-1`} > {item} </a>
                        ) : null;
                        })
                    : null}
                    <a
                    className="btn btn-icon btn-sm btn-light btn-hover-dark me-2 my-1 f-center"
                    onClick={(e) => onNextNavigateClick(e)}
                    >
                    <i className="fa-solid fa-caret-right icon-md icon-xs"></i>
                    </a>
                </div>

                 <div className="d-flex align-items-center py-1 shwoing-filter">
                   <select className="form-control form-control-sm font-weight-bold me-4 border-0 bg-light false" style={{ width: 75 }} onChange={(e)=>onUpdateDataLimit(e)} value={limit} >
                    <option className="" value={10}> 10 </option>
                    <option className="" value={20}> 20 </option>
                    <option className="" value={50}> 50 </option>
                    </select>
                    <span className="react-bootstrap-table-pagination-total">
                    &nbsp;Showing rows {paginateData.currentPage} to&nbsp;
                    {paginateData.totalPages} of&nbsp;{total}
                    </span>
                </div> 
            </div>
        ) : null}
        </>
    );
    };

    const Paggination = React.memo(PN);

    export default Paggination;
