    import { Button } from "react-bootstrap";
    import Loader from "react-loader-spinner";
    import SVG from "react-inlinesvg";
    import React, { useEffect } from "react"; 

    const PS = (props) => {
    const { isPdfDownloading, analyticsStatus, updateSearchText, placeholder } = props;

    const onSearchText = () => {
        let textInput = document.getElementById("searchTextInfluencer");
        let timeout = null;
        textInput.addEventListener("input", (e) => {
        clearTimeout(timeout);
        timeout = setTimeout(() => updateSearchText(textInput.value), 1000);
        });
    };

    useEffect(() => {
        onSearchText()
    }, []);

    return (
        <div className="d-flex align-items-center justify-content-end mb-3">
        <form onSubmit={(e) => e.preventDefault()}>
            <div className="form-group m-0">
            <div className="input-icon input-icon-right position-relative">
                <input className="form-control search-input" id="searchTextInfluencer" type="text" placeholder={placeholder ? placeholder : `Search Post...`} />
                <span> <i className="fa fa-search icon-md text-muted"></i> </span>
            </div>
            </div>
        </form>
        </div>
    );
    };

    const PopupSearch = React.memo(PS);
    export default PopupSearch;
